const initWorkflow = () => {
  window.FinsweetAttributes ||= [];
  window.FinsweetAttributes.push([
    "list",
    (listInstances) => {
      for (const listInstance of listInstances) {
        listInstance.addHook("start", () => {
          listInstance.items.value = listInstance.items.value.slice(0, 5);
        });
      }
      // Your code goes here.
    },
  ]);
};

initWorkflow();
